import React, { Component } from 'react';
import {
  Badge,
  Button,
  Card,
  CardBody,
  CardHeader,
  Col,
  Form,
  FormGroup,
  Input,
  Label,
  Row,
} from 'reactstrap';
import TagsInput from 'react-tagsinput';
import 'react-tagsinput/react-tagsinput.css'
class Forms extends Component {
  constructor(props) {
    super(props);
    this.state = {
      "name":"",
      "availableSeat":"",
      "fees":"",
      "duration":"",
      "notation":"",
      "tags": []
    };
      this.handleTag = this.handleTag.bind(this);
      this.handleChange = this.handleChange.bind(this);
      this.register = this.register.bind(this);
  }
       handleChange(event,comingName) {
         if(comingName == 'name')
           {
                this.setState({name:event.target.value});
           }
           if(comingName == 'notation')
           {
                this.setState({notation:event.target.value});
           }
         if(comingName == 'fees')
          {
               this.setState({fees:event.target.value});
          }
          if(comingName == 'duration')
               {
                    this.setState({duration:event.target.value});
               }
           if(comingName == 'availableSeat')
               {
                    this.setState({availableSeat:event.target.value});
               }
        }
        handleTag(tags) {
          this.setState({tags})
        }
    register(event) {
    fetch('https://api.cedu.in/V2/api/addCourse', {
      method: 'post',
      headers: {
        'Accept': 'application/json',
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({
        name: this.state.name,
        courseNotation:this.state.notation,
        duration: Number(this.state.duration),
        fees: Number(this.state.fees),
        subject:this.state.tags,
        availableSeat: Number(this.state.availableSeat)
      })
    }).then((Response) => Response.json())
      .then((Result) => {
        console.log(Result)
        if (Result.response_code == 200){
           alert(" New Course registered successfully");
           this.props.history.push("/Dashboard");
        }     
        else{
          alert('Server error !!!!!Please Try Again');
        }
          
      })
  }
  render() {
    return (
      <div className="animated fadeIn">
        <Row>
          <Col xs="12">
              <Card>
                <CardHeader>
                  <i className="fa fa-edit"></i>Course Registration Form
                </CardHeader>
                  <CardBody>
                    <Form className="form-horizontal">
                      <Row>
                      <Col xs="12" md="6">
                      <FormGroup>
                        <Label htmlFor="prependedInput">Course Name</Label>
                        <div className="controls">
                            <Input id="prependedInput" size="16" type="text" onChange={(event)=> {this.handleChange(event,'name')}}/>
                        </div>
                      </FormGroup>
                      </Col>
                      <Col xs="12" md="6">
                      <FormGroup>
                        <Label htmlFor="prependedInput">Course Notation</Label>
                        <div className="controls">
                            <Input id="prependedInput" size="16" type="text" onChange={(event)=> {this.handleChange(event,'notation')}}/>
                        </div>
                      </FormGroup>
                      </Col>
                      </Row>
                      <Row>
                      <Col xs="12" md="12">
                      <Label htmlFor="prependedInput">Teaching Subject</Label>
                        <TagsInput id = "prependedInput" value={this.state.tags} onChange={this.handleTag} />
                      </Col>
                      </Row>
                      <Row>
                          <Col xs="12" md="6">
                            <FormGroup>
                              <Label htmlFor="appendedInput">Duration</Label>
                              <div className="controls">
                                  <Input id="appendedInput" size="16" type="text" onChange={(event)=> {this.handleChange(event,'duration')}}/>
                              </div>
                            </FormGroup>
                          </Col>
                          <Col xs="12" md="6">
                            <FormGroup>
                               <Label htmlFor="appendedInput">Available Seat</Label>
                              <div className="controls">
                                  <Input id="appendedInput" size="16" type="text" onChange={(event)=> {this.handleChange(event,'availableSeat')}}/>
                              </div>
                            </FormGroup>
                            </Col>
                      </Row>
                       
                            <FormGroup>
                               <Label htmlFor="appendedInput">Fees</Label>
                              <div className="controls">
                                  <Input id="appendedInput" size="16" type="text" onChange={(event)=> {this.handleChange(event,'fees')}}/>
                              </div>
                            </FormGroup>
                      <div className="form-actions">
                        <Button type="submit" color="primary" onClick={this.register}>Save changes</Button>
                        <Button color="secondary">Cancel</Button>
                      </div>
                    </Form>
                  </CardBody>
              </Card>
          </Col>
        </Row>
      </div>
    );
  }
}

export default Forms;
